.section-block {
  &--food {
    padding-top: 4rem;
  }
  margin-right: 2rem;
  margin-left: 2rem;
  &:not(:last-child) {
    padding-bottom: 2.4rem;
    margin-bottom: 2.4rem;
    border-bottom: 0.4rem solid var(--color-grey-100);
  }
}

.section-title {
  font-size: var(--font-size-20);
  color: var(--color-grey-500);
  margin-bottom: 0.8rem;
  line-height: 1.5;
}

.section-text {
  font-size: var(--font-size-14);
  color: var(--color-grey-500);
  line-height: 1.5;
  margin-bottom: 1.2rem;
  &--no-margin {
    margin-bottom: 0;
  }
}

.section-img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}