:root {
    font-size: 10px;
    --color-bg: #F7F7F7;
    --color-grey-100: #ECECEC;
    --color-grey-500: #666666;
    --color-main-500: #54A8E5;
    --font-size-32: 3.2rem;
    --font-size-20: 2rem;
    --font-size-18: 1.8rem;
    --font-size-16: 1.6rem;
    --font-size-14: 1.4rem;
    --border-radius-16: 1.6rem;
    --border-radius-8: 0.8rem;
}

html {
    font-family: 'Roboto', 'Noto Sans TC', "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
    &.is-fixed {
        overflow: hidden;
    }
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    font-variant-east-asian: traditional;
    background-color: var(--color-bg);
    &.is-fixed {
        overflow: hidden;
    }
}

.wrap {
    max-width: 48rem;
    margin: 0 auto;
    overflow-x: hidden;
}

.inner {
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    
}

.user {
    h2 {
        font-size: var(--font-size-32);
        color: var(--color-main-500);
        line-height: 1.5;
    }
}

.nutrients-list {
    display: flex;
    flex-wrap: wrap;
    gap: 0.8rem;
    &__item {
        font-weight: 500;
        color: var(--color-main-500);
        border: solid 0.2rem var(--color-main-500);
        padding: 0.8rem 1.2rem;
        border-radius: 4rem;
        font-size: var(--font-size-14);
        &--highlight {
            font-weight: 900;
        }
    }
}

.recommend {
    h4 {
        font-size: var(--font-size-32);
        color: var(--color-main-500);
        line-height: 1.5;
    }
}

.ingredients {
    .swiper {
        box-sizing: border-box;
        padding-left: 2rem;
        padding-right: 4rem;
        width: calc(100% + 6rem);
        position: relative;
        transform: translate3d(-2rem,0,0);
        height: 100%;
    }
    .swiper-pagination-bullet-active {
        background-color: var(--color-main-500);;
    }
    .swiper-pagination-horizontal {
        bottom: 0;
    }
    &__img {
        margin-bottom: 0.8rem;
        img {
            border-radius: 1.6rem;
            width: 100%;
            display: block;
        }
    }
    &__item {
        h4 {
            text-align: center;
            font-size: var(--font-size-16);
            color: var(--color-grey-500);
        }
    }
}